<template>
  <div class="row music-scrollCon" v-if="userData">
    <Login v-if="dom.login"/>
    <loading :active.sync="isLoading">
      <img alt="Meason" src="../../assets/img/loading.svg" width="100">
    </loading>
    <div class="col-12" v-if="!songList">暫無資料</div>
    <div class="col-xl-3 col-lg-3 col-md-4 col-sm-6"
      v-for="(item, index) in songList" :key="item.id + index">
      <a class="song-likes" href="#"
        v-if="userData"
        title="加入最愛"
        @click.prevent="liked(item, index)"
        v-html="comLike(item.like)"></a>
      <a class="song-likes" href="#"
        v-else title="加入最愛"
        @click.prevent="dom.login = true;dom.opened = false">
        <i class="far fa-heart"></i>
      </a>
      <div class="music-con">
        <div class="photo"
          @click="togglePlay(index)"
          :class="{'playing': player.id === item.id}"
          :style="{'backgroundImage': `url(${item.cover})`}">
          <img src="../../assets/img/bg/scale.png" width="100%" alt="">
          <button class="click"><i class="far fa-play-circle"></i></button>
          <p class="views">{{item.view}} <small>瀏覽</small></p>
        </div>
        <div class="text">
          <router-link :to="`/music/${item.id}`" tag="h3">{{item.name}}</router-link>
          <h4 class="type">
            <i class="fas fa-music"></i>
            <p class="type"><span v-for="i in item.type" :key="i">{{i}}</span></p>
          </h4> 
        </div>
        <div class="artist">
          <router-link to="/user" :title="item.artist">
            <i class="fas fa-user"></i> {{item.artist}}
          </router-link>
          <span v-if="item.like">{{item.like.length}} 收藏</span>
        </div>
      </div>
    </div>
    <!-- Modal -->
    <div class="modal fade" id="filter-modal" tabindex="-1" role="dialog" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">快速篩選</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12">
                <div class="input-group mb-3">
                  <input type="text" class="form-control" placeholder="♫ Search" 
                    aria-label="" aria-describedby="basic-addon1" v-model="tempDom.filterInput">
                </div>	
              </div>
            </div>
            <div class="row" id="instrumentItem">
              <div class="col-md-4 col-sm-4 col-6" 
                v-for="(item, index) in tempDom.filterItem" :key="index"
                v-show="item">
                <div class="radio">
                  <input type="radio" name="radio"
                    v-model="tempDom.selectType" :id="`radio${index}`" :value="item">
                  <label :for="`radio${index}`">
                    {{item}}
                  </label>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" data-dismiss="modal"
            @click="filterData()">確定</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase';
import $ from 'jquery';
import Login from '../../components/Login.vue'

export default {
  name: 'AllSong',
  props: ['userData'],
  data() {
    return {
      isLoading: false,
      dom:{
        login: false,
        opened: false,
      },
      player: {
        id: '',
        pause: false,
      },
      songList: null,
      tempDom: {
        deleteOpen: false,
        filterInput: '',
        filterItem: ['全部','編曲/作詞', 'COVER', '搖滾', '貝斯手', '電子琴手', '提琴樂手', '電吉他手', '木吉他手', '爵士鼓手', '鋼琴手', '木箱鼓手', 'DJ', 'B-BOX夥伴', '阿卡貝拉夥伴', '長笛手', '薩克斯風樂手', '木管樂手', '銅管樂手(法國號,小號,低音大號,長號等)', '吹管類樂手(笛,蕭,嗩吶,笙)', '彈拔類樂手(琵琶,箏,揚琴,中阮)', '拉弦類樂手(胡)', '打擊鼓手(大鼓,小軍鼓)', '其他'],
      },
      tempData: {
        allData: [],
        firstPlay: true,
        options: ['全部','編曲/作詞', 'COVER', '搖滾', '貝斯手', '電子琴手', '提琴樂手', '電吉他手', '木吉他手', '爵士鼓手', '鋼琴手', '木箱鼓手', 'DJ', 'B-BOX夥伴', '阿卡貝拉夥伴', '長笛手', '薩克斯風樂手', '木管樂手', '銅管樂手(法國號,小號,低音大號,長號等)', '吹管類樂手(笛,蕭,嗩吶,笙)', '彈拔類樂手(琵琶,箏,揚琴,中阮)', '拉弦類樂手(胡)', '打擊鼓手(大鼓,小軍鼓)', '其他'],
      }
    }
  },
  watch: {
    'tempDom.filterInput': {
      handler: function filterItems(val) {
        const vm = this;
        vm.tempDom.filterItem = vm.tempData.options.map(function(item, index, array){
          if (item.indexOf(val) !== -1) {
            return item;
          }
           return false;
        });
      }
    },
  },
  components:{
    Login
  },
  methods: {
    togglePlay(index) {
      const vm = this;
      let newSongList = []; // 複製一個 Arr 用
      if(!vm.player.pause || vm.songList[index].id!==vm.player.id) {
        if(vm.tempData.firstPlay) {
          vm.songList.forEach(i => {
            newSongList.push(i)
          });
          newSongList.unshift(vm.songList[index]);
          newSongList.splice(index + 1, 1);
          this.$bus.$emit('audio:list', newSongList);
          vm.player.pause = true;
          vm.tempData.firstPlay = false;
        } else {
          this.$bus.$emit('audio:play', vm.songList[index]);
          vm.player.pause = true;
        }
        // vm.player.id = vm.songList[index].id;
      } else {
        this.$bus.$emit('audio:pause');
        vm.player.pause = false;
        vm.player.id = '';
      }      
    },
    getloveSong() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getLoveSong`;
      vm.isLoading = true;
      vm.$http.post(url, {"id": vm.userData.uid}).then((res) => {
        if(res.data[0]){
          vm.getMusicData()
        } else {
          vm.songList = null;
        }
        vm.isLoading = false;
      })
    },
    getMusicData() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getLoveSong`;
      let allData = null;
      // vm.isLoading = true;
      vm.$http.post(url, {"id": vm.userData.uid})
      .then((response) => {
        allData = response.data;
        vm.isLoading = false;
      })
      .then((result) => {
        allData.forEach(val => {
          if(val.like) {val.like = Object.keys(val.like); }         
        });
      })
      .then(result => {
        allData.forEach(val => {
          if(val.type) {val.type = Object.values(val.type); }         
        });
      })
      .then(() => {
        this.$bus.$emit('audio:newList', allData);
        vm.tempData.allData = allData;
        if(vm.tempDom.selectType) {
          vm.filterData();
        } else {
          vm.songList = allData;
        }
      })
      .catch(e => {
        vm.isLoading = false;
      })
    },
    comLike(item) {
      const vm = this;
      const user = vm.userData.uid;
      let str = '';
      if(Array.isArray(item)){
        if(item.indexOf(user) !== -1){
          str = '<i class="fas fa-heart"></i>'
        } else {
          str = '<i class="far fa-heart"></i>'
        }
      } else {
        str = '<i class="far fa-heart"></i>'
      }
      return str
    },
    liked(item, index) {
      // 按讚
      const vm = this;
      const user = vm.userData.uid;
      if(vm.userData) {
        if(item.like) {
          if(item.like.indexOf(user) !== -1) {
            vm.sendLikeAPI(item, 'unlike');
          } else {
            vm.sendLikeAPI(item, 'likes')
          }          
        } else {
          vm.sendLikeAPI(item, 'likes')
        }
      }      
    },
    sendLikeAPI(item, method) {
      // 按讚 API
      const vm = this;
      const url = `${process.env.VUE_APP_API}/likeMusic`;
      const reqData = {
        method: method,
        userId: vm.userData.uid,
        musicId: item.id,
        posterId: item.poster_uid,
      }
      vm.$http.post(url, {'reqData':reqData}).then((response) => {
        if(response.data.success){
          vm.getloveSong();
        }
      })
    },
    scrollEvent() {
      // 暫時不會用到
      const sclTop = document.querySelector('.music').scrollTop;
      const conTop = $('.music').height() || document.body.clientHeight;
      const top = parseInt(sclTop + conTop);
      const elH = parseInt($('.music-scrollCon').height(), 10) + 71;
      if (top === elH) {
        console.log(top, elH)
      }
    },
    filterData() {
      const vm = this;
      if(vm.tempDom.selectType !== '全部') {
        let data = vm.tempData.allData.filter((item) => {
          return vm.ArrIndexOf(item.type)
        })
        vm.songList = data;
      } else {
        vm.songList = vm.tempData.allData;
      }
    },
    ArrIndexOf(arr) {
      const vm = this;
      // const keys = Object.keys(obj).map(key => obj[key]);
      return arr.indexOf(vm.tempDom.selectType) !== -1
    },
  },
  created() {
    const vm = this;
    vm.getloveSong();
    vm.$bus.$on('current:id',(id = '') => {
      vm.player.id = id;
    })
    vm.$bus.$on('current:pause',(element = '') => {
      vm.player.id = '';
      vm.player.pause = false;   
    })
    vm.$bus.$on('close:login',(val) => {
      vm.dom.login = val;
    })
    vm.$bus.$on('add:view',(obj = '') => {
      vm.songList.forEach((item, index) => {
        if(item.id === obj.id) {
          vm.songList[index].view = obj.num;
        }
      });
    })
  },
}
</script>

<style lang="scss" scoped>
.aplayer.aplayer-narrow{
  width: 100%;
  height: 150px;
  .aplayer-pic{
    width: 100%;
    height: 150px;
  }
}
.container{
  width: 95%;
  .music-scrollCon{
    margin-bottom: 70px;
  }
  @media (max-width: 576px){
    .user-title h2{
      font-size: 20px;
    }    
  }
  @media (min-width: 1200px){
    max-width: 900px;
  }
  @media (min-width: 1500px){
    max-width: 1150px;
    .col-xl-3 {
      flex: 0 0 20%;
      max-width: 20%;
    }
  }
}

</style>
